import {
 //busiitemDeleted,
//busiitemDetail,
	busiitemPages,
	busiitemAdd,
	busiitemUpdate,
	busiitemDelete,
} from 'api/api';
import storyform from './businessComponents/goodsManagForm';
let pageData = {
	busiInfoId:'',
	busiItemId:'',
//	createTime:""
	creator:'',
	currencyUnit:'',
	currencyUnitText:'',
	discountPrice:'',
	itemEnUsName:'',
	itemEnUsTitle:'',
	itemIcon:'',
	itemJaJpName:'',
	itemJaJpTitle:'',
	itemKoKrName:'',
	itemKoKrTitle:'',
	itemName:'',
	itemPrice:'',
	itemTitle:'',
	itemTotal:'',
	itemZhCnName:'',
	itemZhCnTitle:'',
	//modifyTime:"2017-04-10 16:51:28"
	//modifyor:"admin"
	status:'',
	uom:'',
	uomText:'',
	busiItemType:''
}
export default {
    data() {
        return {
            searchValue: '',
            tableData:{},
            dialogVisible: false,
            dialogTyle: '',
            pageData: {
                ...pageData
            },
            page: {
                start: 0,
                length: 10,
                draw: 1,
            },
            currentPage: 1,
            pageSizes: [10, 20, 30],
        }
    },

    components: {
        storyform
    },
    created() {
        this.init()
    },
    watch: {
      searchValue(value){
        if(!value){
          busiitemPages(this.page).then(res => {
            	this.tableData = res.data.obj
            })
        }
      }
    },
    methods: {
        init() {
            busiitemPages(this.page).then((res) => {
				this.tableData = res.data.obj
//          	this.tableData.data = res.data.obj
//          	this.tableData.recordsTotal=5
            })
        },
        onQuery() {
        	if(this.searchValue){
	          busiitemPages({...this.page,itemName:this.searchValue}).then(res => {
					this.tableData = res.data.obj
				})
	        }
        	
        },
        showDilog(type, obj = pageData) {
            this.dialogVisible = true
            this.dialogTyle = type
            if(!obj.busiItemType){
            	obj.busiItemType=''
            }
            this.pageData = { ...obj }

        },
        onSubmit() {
            this.$refs.storyForm.validate(valid => valid && this.commitForm())
        },
        commitForm() {
//      	for(let key in this.pageData ){
//      		if(key.indexOf('Time')>-1){
//      			delete  this.pageData[key]
//      		}
//      	}
        	let formData = new FormData();
				for(let key in this.pageData) {
					if(key == 'itemIcon') {
						formData.append('multipartFile', this.pageData.itemIcon)// this.bootPageObj[key]
					} else {
						formData.append(key, this.pageData[key]);
					}
				}
            if (this.dialogTyle === 'edit') {
	
                busiitemUpdate(formData).then(res => {
                    this.init()
                    this.dialogVisible = false
                })
            } else if (this.dialogTyle === 'add') {
                busiitemAdd(formData).then(res => {
                    this.init()
                    this.dialogVisible = false
                })
            }
        },
        handleDelete(obj) {
            this.$confirm(this.$t('commom.deleConfim'), '', {
                confirmButtonText: this.$t('commom.sure'),
                cancelButtonText: this.$t('commom.cancel'),
                type: 'warning'
            }).then(() => {
            	
                busiitemDelete({
                    busiItemId: obj.busiItemId
                }).then(res => {
                    this.init()
                })

            }).catch(res => {

            })


       },
       getpageData(){
	       	 busiitemPages({...this.page,itemName:this.searchValue}).then(res => {
				this.tableData = res.data.obj
			})
        },
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(()=>{
	        	this.getpageData()
	        },0)
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(()=>{
	        	this.getpageData()
	        },0)
		},
    }
}



